import Vue from 'vue';
Vue.directive('scroll', {
    bind(el, binding) {
      el.addEventListener('scroll', function() {

          /*
           * scrollHeight ()
           * scrollTop ,, , , scrollTop0.
           * clientHeight ()
           * , true, false:
           * ele.scrollHeight - ele.scrollTop === ele.clientHeight;
           */
          const CONDITION = this.scrollHeight - this.scrollTop <= this.clientHeight;
          if(CONDITION) {
              binding.value();
          }
      });
    }
});